.banner-slider {
  position: relative;
  z-index: 1;
  margin-top: 8px;
  margin-bottom: 24px;

  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 38px;
    background-color: rgba(#fff, 0.8);
    position: absolute;
    z-index: 10;
    bottom: 0;
    right: 0;
    opacity: 0;
    transition: 0.3s;
  }

  &:hover::before {
    opacity: 1;
  }

  &__frame {
    display: block;
    position: relative;

    img {
      display: block;
      width: 100%;
      height: 320px;
      object-fit: cover;
      object-position: 50% 50%;
    }
  }

  &__pagination {
    width: auto;
    font-size: 12px;
    position: absolute;
    z-index: 10;
    top: 10px;
    left: auto;
    bottom: auto;
    right: 10px;
    opacity: 0;
    transition: 0.3s;
  }

  &:hover &__pagination {
    opacity: 1;
  }
}

.about-slider {
  position: relative;
  z-index: 1;

  &__wrapper {
    position: relative;
    z-index: 1;
  }

  &__frame {
    @include frame("rect", 56.25%);

    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 10;
      transition: 0.3s;
    }
  }

  &__heart {
    display: flex;
    width: 40px;
    height: 40px;
    cursor: pointer;
    margin: 0;
    position: absolute;
    z-index: 20;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: 0.3s;

    span {
      &::after {
        content: "\f08a";
        font-size: 24px;
        font-family: "fontawesome";
        color: $white;
      }
    }

    input {
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      opacity: 0;
    }

    input:checked + span::after {
      content: "\f004";
      color: $primary;
    }
  }

  &__wrapper:hover &__frame::after {
    background-color: rgba(#000, 0.3);
  }

  &__wrapper:hover &__heart {
    opacity: 1;
  }

  &__prev,
  &__next {
    display: flex;
    width: 10%;
    height: 100%;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background: linear-gradient(to right, rgba(#000, 0.3), rgba(#000, 0));
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0;

    &:focus {
      outline: 0;
    }

    &::after {
      content: "";
      display: block;
      width: 30px;
      height: 30px;
      border-top: 2px solid #eee;
      border-left: 2px solid #eee;
      transform: rotateZ(-45deg);
    }
  }

  &__next {
    left: auto;
    right: 0;
    background: linear-gradient(to left, rgba(#000, 0.3), rgba(#000, 0));

    &::after {
      transform: rotateZ(135deg);
    }
  }
}
