.footer {
  border-top: 20px solid #a0a09f;

  &__main {
    padding: 24px 0;
  }

  &__grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 290px;
    grid-gap: 24px;
  }

  &__title {
    font-size: 20px;
    font-weight: 400;
    margin-bottom: 10px;
  }

  &__contact {
    font-size: 14px;
    line-height: 25px;

    a {
      color: inherit;
    }
  }
}

.f-menu {
  list-style: none;
  margin: 0;
  padding: 0;

  &__item {
    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }

  &__link {
    display: inline-block;
    font-size: 14px;
    color: $gray-800;

    &:hover {
      color: $gray-800;
      text-decoration: none;
    }
  }
}

.endpage {
  border-top: 1px solid $border-color;
  border-bottom: 1px solid $border-color;
  padding: 30px 0;
  margin-bottom: 5px;

  &__inner {
    display: flex;
    align-items: center;
  }

  &__logo {
    display: inline-flex;
    margin-right: 18px;

    img {
      height: 50px;
    }
  }

  &__info {
    margin-left: auto;
  }
}
