.list {
  &__item {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }
}

.list-2 {
  &__item {
    &:not(:last-child) {
      margin-bottom: 41px;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        bottom: -21px;
        border-bottom: 1px dashed $border-color;
        left: 0;
        right: 42px;
      }
    }
  }
}

.list-3 {
  margin-bottom: 26px;

  &__item {
    border-bottom: 1px dotted $border-color;
    padding-bottom: 24px;
    margin-bottom: 24px;
  }
}

.list-4 {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-gap: 12px;
  list-style: none;
  border-top: 1px solid $border-color;
  padding: 20px 0;
  margin: 50px 0 0;
}

.list-5 {
  &__item {
    &:not(:last-child) {
      margin-bottom: 36px;
    }
  }
}

.faq-list {
  padding-left: 14px;
  margin: 0;

  &__link {
    font-size: 14px;
    line-height: 25px;
    color: inherit;

    &:hover {
      color: $primary;
      text-decoration: none;
    }
  }
}
