.section {
  display: flex;
  flex-direction: column;
  margin-bottom: 50px;

  &__header {
    margin-bottom: 24px;
    border-bottom: 1px solid $border-color;
  }

  &__title {
    font-size: 30px;
    font-weight: 400;
    margin-bottom: 0;
    position: relative;
    line-height: 1.2;
    padding: 10px 0 1px 27px;

    a:hover {
      color: inherit;
    }

    &::before {
      content: "";
      display: block;
      width: 14px;
      height: 37px;
      background-color: $primary;
      position: absolute;
      bottom: 10px;
      left: 0;
    }
  }

  &__body {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
}

.section-2 {
  &__title {
    font-size: 25px;
    font-weight: 600;
    color: $primary;
    border-bottom: 1px solid $border-color;
    margin-bottom: 12px;
  }
}

.section-3 {
  &__title {
    font-size: 16px;
    font-weight: 600;
    border-bottom: 1px solid $border-color;
    padding-bottom: 3px;
    margin-bottom: 12px;
  }
}
