.header {
  height: 100px;
  position: relative;
  z-index: 100;

  &__wrapper {
    width: 100%;
    background-color: $white;
    border-bottom: 1px solid $border-color;
    position: fixed;
    top: 0;
    left: 0;
  }

  &__inner {
    display: flex;
    align-items: center;
    position: relative;
  }

  &__logo {
    display: inline-flex;
    margin: 8px 0;

    img {
      height: 50px;
    }
  }

  &__menu {
    display: flex;
    margin-left: auto;
  }

  &__menu-item {
    display: inline-block;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    color: #4a4848;
    transition: 0.4s;

    &:hover {
      color: $yellow-darker;
      text-decoration: none;
    }

    &:not(:last-child) {
      margin-right: 40px;
    }
  }
}

.toppage {
  background-color: #e3e1e1;

  &__inner {
    display: flex;
    align-items: center;
    height: 33px;
  }

  &__text {
    margin-right: auto;
    padding-right: 20px;
    font-size: 12px;
    text-transform: uppercase;
    color: #747474;
  }
}

.lang {
  display: flex;
  align-items: center;
  margin-right: 30px;

  &__item {
    display: inline-block;
    font-size: 12px;
    color: $gray-800;
    transition: 0.3s;

    &:hover,
    &.active {
      color: $primary;
      text-decoration: none;
    }

    &:not(:last-child) {
      margin-right: 21px;
      position: relative;

      &::after {
        content: "";
        display: block;
        height: 9px;
        border-right: 1px solid #666;
        position: absolute;
        top: 50%;
        right: -11px;
        transform: translateY(-50%);
      }
    }
  }
}
