.card-1 {
  display: flex;
  flex-direction: column;
  border: 1px solid $border-color;
  height: 100%;
  padding: 12px 12px 28px;

  &__frame {
    @include frame("rect", 50%);
  }

  &__overlay {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 8px;
    font-size: 26px;
    font-weight: 500;
    line-height: 1.2;
    color: $yellow-light;
    text-shadow: 0 1px #666, 1px 0 #666, 1px 1px #666, 1px -1px #666,
      -1px 1px #666, -1px -1px #666, 0 -1px #666, -1px 0 #666;
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0;

    &:hover {
      color: $yellow-light;
      text-decoration: none;
    }
  }

  &__desc {
    margin: 20px 0;
    text-align: center;
    color: #373737;
    font-size: 14px;
    line-height: 23px;
  }
}

.card-2 {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 16px 12px;
  border: 1px solid $border-color;

  &__title {
    font-size: 16px;
    font-weight: 600;
    margin: 0;
    line-height: 20px;

    a:hover {
      color: inherit;
    }
  }

  &__posted {
    font-size: 13px;
    font-weight: 400;
    color: $blue-light;
    line-height: 20px;
  }

  &__frame {
    @include frame("rect", 50%);
    margin-top: 16px;
  }

  &__detail {
    font-size: 14px;
    font-weight: 400;
    line-height: 23px;
    color: #050505;
    margin: 14px 0 10px;
  }

  &--p20 {
    padding: 20px;
  }
}

.card-3 {
  display: flex;
  align-items: flex-end;
  height: 100%;
  position: relative;
  z-index: 1;
  padding-top: 150px;

  &__bg {
    @include img("bg");
  }

  &__content {
    width: 270px;
    max-width: 100%;
    background-color: rgba(236, 236, 236, 0.7);
    padding: 16px 20px;
    border: 1px solid transparent;
  }

  &__title {
    font-size: 16px;
    font-weight: 600;
    margin: 0;
    line-height: 20px;

    a:hover {
      color: inherit;
    }
  }

  &__time {
    font-size: 13px;
    line-height: 21px;
    color: $blue-light;
  }

  &__posted {
    font-size: 13px;
    font-weight: 400;
    color: $blue-light;
    line-height: 20px;
  }

  &__frame {
    @include frame("rect", 50%);
    @include frame("zoom");
    margin-top: 16px;
  }

  &__detail {
    font-size: 14px;
    font-weight: 400;
    line-height: 23px;
    color: #050505;
    margin: 14px 0 10px;
  }

  &--right {
    justify-content: flex-end;
  }
}

.card-4 {
  height: 100%;

  &__frame {
    @include frame("rect", 60%);
    height: 100%;
  }

  &__body {
    display: flex;
    flex-direction: column;
    padding: 0 16px;
    height: 100%;
  }

  &__title {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 0;

    a:hover {
      color: inherit;
    }
  }

  &__desc {
    font-size: 14px;
    line-height: 30px;
    margin-top: auto;
    position: relative;
    top: 11px;
  }
}

.card-5 {
  display: flex;
  height: 100%;
  justify-content: center;
  flex-direction: column;
  background: url("../images/card-5-bg.webp") center/cover no-repeat;
  padding: 40px;
  font-size: 35px;
  font-weight: 600;
  color: $black;
  text-shadow: 0 2px 5px rgba(#fff, 1);

  &__text {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}

.news {
  align-items: stretch;

  &__frame {
    @include frame("rect", 75%);
    width: 230px;
    margin-right: 18px;
  }

  &__body {
    display: flex;
    flex-direction: column;
  }

  &__title {
    @include max-line(2);
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 4px;
  }

  &__time {
    font-size: 13px;
    color: $blue-light;
    line-height: 21px;
    margin-bottom: 6px;
  }

  &__desc {
    @include max-line(3);
    margin-bottom: 14px;
  }
}

.news-2 {
  &__frame {
    @include frame("rect", 70%);
    width: 111px;
    border: 1px solid $border-color;
    margin-right: 10px;
    align-self: stretch;
  }

  &__body {
    display: flex;
    flex-direction: column;
    align-self: stretch;
  }

  &__title {
    @include max-line(2);
    font-size: 14px;
    font-weight: 700;
  }
}

.news-3 {
  &__frame {
    @include frame("rect", 76%);
    height: 100%;
    padding-bottom: 1px;
  }

  &__body {
    display: flex;
    flex-direction: column;
  }

  &__title {
    @include max-line(2);
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 8px;
  }

  &__time {
    font-size: 13px;
    line-height: 21px;
    color: $blue-light;
    margin-bottom: 8px;
  }

  &__desc {
    @include max-line(8);
    text-align: justify;
    margin-bottom: 24px;
  }
}

.event {
  align-items: center;

  &__time {
    display: flex;
    flex-direction: column;
    width: 57px;
    height: 53px;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: $white;
    line-height: 1.2;
    font-size: 700;
    margin-right: 14px;
  }

  &__title {
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 0;

    a:hover {
      color: inherit;
    }
  }
}

.event-2 {
  &__frame {
    @include frame("rect", 56.25%);
    border: 1px solid $border-color;
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 0;
    margin-top: 10px;
  }
}

.event-3 {
  &__frame {
    @include frame("rect", 60%);
  }

  &__body {
    padding-left: 21px;
  }

  &__title {
    font-size: 40px;
    font-weight: 600;
    line-height: 1.5;
    margin-bottom: 1.5em;
    color: #373737;
  }

  &__info {
    font-size: 30px;
    font-weight: 600;
  }
}

.about {
  background-color: #e3e1e1;

  &--reverse {
    flex-direction: row-reverse;
  }

  &__frame {
    @include frame("rect", 50%);
    height: 100%;
  }

  &__body {
    padding: 20px;
  }

  &__title {
    font-size: 25px;
    font-weight: 400;
    line-height: 30px;
    margin-bottom: 13px;

    a:hover {
      color: inherit;
    }
  }
}

.story {
  &__frame {
    @include frame("rect", 54.37%);
    border: 1px solid $border-color;
  }

  &__title {
    font-size: 14px;
    font-weight: 400;
    margin: 14px 0 0;
  }
}

.story-2 {
  &__frame {
    @include frame("rect", 58.43%);
  }

  &__body {
    display: flex;
    flex-direction: column;
    padding: 14px 0 0 60px;
    height: 100%;
  }

  &__title {
    font-size: 40px;
    font-weight: 600;
    line-height: 1.225;
    color: #373737;
    margin-bottom: 25px;
  }

  &__info {
    font-size: 30px;
    line-height: 1.233;
    color: #747474;
    margin-bottom: 25px;
  }
}

.person-intro {
  height: 100%;
  text-align: justify;
  border: 1px solid $border-color;
  padding: 20px;

  p:not(:last-child) {
    margin-bottom: 28px;
  }

  ul {
    padding-left: 28px;
    margin-bottom: 28px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.profile {
  height: 100%;
  border: 1px solid $border-color;
  padding: 16px;

  &__frame {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 14px;
  }

  &__name {
    font-size: 16px;
    font-weight: 600;
    text-transform: uppercase;
    text-align: center;
    color: $primary;
  }

  &__info {
    text-align: center;
    margin-bottom: 20px;

    a,
    a:hover {
      color: inherit;
      text-decoration: none;
    }
  }

  &__contact {
    text-align: center;
    margin-bottom: 8px;

    a,
    a:hover {
      color: inherit;
      text-decoration: none;
    }
  }
}

.partner {
  @include frame("rect", 68.8888%);
  border: 1px solid $border-color;
}

.partner-2 {
  @include frame("rect", 51.86%);
  border: 1px solid $border-color;
}

.partner-3 {
  &__frame {
    @include frame("rect", 47.17%);
    width: 212px;
    border: 1px solid $border-color;
    margin-right: 24px;
  }

  &__body {
    display: flex;
    flex-direction: column;
    align-self: stretch;
    text-align: justify;
  }

  &__title {
    font-size: 16px;
    font-weight: 600;
    margin-top: -5px;
    margin-bottom: 4px;

    a:hover {
      color: inherit;
    }
  }

  &__info {
    margin-bottom: 4px;
  }

  &__link {
    position: relative;
    top: 5px;
    font-style: italic;

    &,
    &:hover {
      color: $primary;
      text-decoration: none;
    }
  }
}

.detail-table {
  table {
    width: 100%;
    border: 1px solid $border-color;
    margin-bottom: 0;

    tbody {
      td {
        border: 1px solid $border-color;
        padding: 3px 13px;
        vertical-align: baseline;
        font-size: 14px;
        line-height: 28px;

        &:first-child {
          width: 212px;
        }
      }
    }
  }
}

.city {
  position: relative;
  z-index: 1;

  &__frame {
    @include frame("rect", 66.66%);
  }

  &__title {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 10;
    margin: 0;
    background: linear-gradient(to right, rgba(#fff, 0.8), rgba(#fff, 0));

    a {
      display: block;
      font-size: 16px;
      font-weight: 600;
      line-height: 24px;
      text-transform: uppercase;
      color: $primary;
      padding: 8px 14px;
      transition: 0.3s;

      &:hover {
        color: $primary;
        text-decoration: none;
      }
    }
  }
}

.lc-info {
  font-size: 14px;

  &__iwrap {
    position: relative;

    &::before {
      content: "";
      display: block;
      padding-bottom: 56.25%;
    }

    img {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: 50% 50%;
    }
  }

  &__btns {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 8px;
    margin-top: auto;

    .nav-item {
      display: flex;
    }

    .nav-link {
      display: block;
      width: 100%;
      text-align: center;
      font-size: 14px;
      font-weight: 400;
      line-height: 21px;
      padding: 2px 20px 0;
      border: 1px solid $border-color;
      transition: 0.3s;
      background: 0;
      color: $gray-800;

      &:hover {
        border-color: $yellow;
        background-color: $yellow;
        text-decoration: none;
        color: $primary;
      }

      &.active {
        border-color: $primary;
      }
    }
  }

  &__info {
    line-height: 30px;
  }
}

.lc-detail {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 14px;
  &__item {
    display: flex;
    margin-bottom: 9px;
    align-items: baseline;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__label {
    flex: 0 0 170px;
    max-width: 170px;
    margin-right: 16px;
    font-weight: 600;
  }
  &__content {
    text-align: justify;
    flex: 1;
    p {
      margin-bottom: 0;
    }
  }
  @media (max-width: 575px) {
    &__item {
      flex-wrap: wrap;
      margin-bottom: 15px;
    }
    &__label {
      flex: 0 0 100%;
      max-width: 100%;
      margin-right: 0;
      margin-bottom: 5px;
    }
  }
}

.lc-download {
  position: relative;
  &::before {
    content: "";
    display: block;
    padding-bottom: 56.25%;
  }
  &__inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
}
