.pagination {
  margin-bottom: 0;

  .page-link {
    color: $gray-800;
    min-width: 25px;
    text-align: center;
    padding: 1px 6px 0;
    font-size: 14px;
    line-height: 22px;
    border-radius: 0 !important;
    transition: 0.3s;

    &:hover {
      color: $primary;
      background-color: $yellow;
      border-color: $yellow;
    }
  }
}

.breadcrumb {
  border-bottom: 1px solid $border-color;
  margin-bottom: 8px;
  padding: 10px 0 8px;

  &-item {
    &,
    & + &::before,
    &.active,
    &.active::before,
    a {
      color: $gray-800;
    }

    a:hover {
      color: $primary;
      text-decoration: none;
    }
  }

  &-item.active {
    color: $primary;
  }
}

$phoneRingColor: #e73a35;
.phone-ring {
  display: flex;
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
  color: #fff;
  border-radius: 50%;
  background-color: $phoneRingColor;
  position: fixed;
  z-index: 100;
  bottom: 50px;
  left: 50px;
  box-shadow: 0 5px 20px rgba(#000, 0.3);

  &:hover {
    color: $white;
  }

  &::before {
    content: "";
    display: block;
    width: 250%;
    height: 250%;
    border-radius: 50%;
    border: 1px solid $phoneRingColor;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: phone-circle-animate 1s ease-in-out infinite;
  }

  &::after {
    content: "";
    display: block;
    width: 160%;
    height: 160%;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: -1;
    transform: translate(-50%, -50%);
    background-color: rgba($phoneRingColor, 0.5);
    animation: phone-circle-fill-animate 2s ease-in-out infinite;
  }

  &__icon {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    background-color: rgba($phoneRingColor, 0.7);
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
    animation: phone-icon-animate 1s ease-in-out infinite;
  }

  &__text {
    display: inline-block;
    background-color: rgba($phoneRingColor, 0.7);
    font-size: 14px;
    font-weight: 700;
    line-height: 40px;
    padding: 0 20px 0 60px;
    color: $white;
    border-radius: 20px;
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    box-shadow: 0 5px 20px rgba(#000, 0.3);
  }

  &:hover &__text {
    color: $phoneRingColor;
    background-color: rgba(#fff, 0.7);
  }

  @media (max-width: 767px) {
    left: 20px;
    bottom: 20px;

    &__text {
      display: none;
    }
  }
}

@keyframes phone-circle-animate {
  0% {
    transform: translate(-50%, -50%) rotate(0) scale(0.5);
    opacity: 0.1;
  }
  30% {
    transform: translate(-50%, -50%) rotate(0) scale(0.7);
    opacity: 0.5;
  }
  100% {
    transform: translate(-50%, -50%) rotate(0) scale(1);
    opacity: 0.1;
  }
}

@keyframes phone-circle-fill-animate {
  0%,
  100% {
    transform: translate(-50%, -50%) rotate(0) scale(0.7);
    opacity: 0.2;
  }
  50% {
    transform: translate(-50%, -50%) rotate(0) scale(1);
    opacity: 0.2;
  }
}

@keyframes phone-icon-animate {
  0%,
  100% {
    transform: rotate(0) scale(1) skew(1deg);
  }
  10% {
    transform: rotate(-25deg) scale(1) skew(1deg);
  }

  20%,
  40% {
    transform: rotate(25deg) scale(1) skew(1deg);
  }
  30% {
    transform: rotate(-25deg) scale(1) skew(1deg);
  }
  50% {
    transform: rotate(0) scale(1) skew(1deg);
  }
}

.button {
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  padding: 2px 20px 0;
  border: 1px solid $primary;
  transition: 0.3s;
  background: 0;
  color: $primary;

  &:hover {
    border-color: $yellow;
    background-color: $yellow;
    text-decoration: none;
    color: $primary;
  }

  &--red {
    background-color: $primary;
    color: $white;
  }

  &--blue {
    background-color: $info;
    border-color: $info;
    color: $white;
  }

  &--xs {
    font-size: 12px;
    padding: 0 18px;
    line-height: 22px;
  }

  &--md {
    font-size: 16px;
    line-height: 29px;
    min-width: 133px;
    text-align: center;
  }

  &--lg {
    font-size: 18px;
    padding: 2px 30px 0;
    line-height: 36px;
  }

  &--block {
    display: block;
    width: 100%;
    text-align: center;
  }
}

.wrapper {
  width: 100%;
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
}

.event-tabs {
  display: flex;
  flex-direction: column;
  flex-grow: 1;

  &__tabs {
    margin-bottom: 16px;

    .nav-item {
      &:not(:last-child) {
        margin-right: 21px;
        position: relative;

        &::after {
          content: "";
          display: block;
          height: 13px;
          border-right: 1px solid $blue-light;
          position: absolute;
          top: 50%;
          right: -11px;
          transform: translateY(-50%);
        }
      }
    }

    .nav-link {
      font-size: 18px;
      font-weight: 300;
      padding: 0;
      color: $gray-800;

      &.active {
        color: $primary;
        font-weight: 600;
      }
    }
  }

  &__body {
    height: 282px;
    flex-grow: 1;
    position: relative;
  }

  &__content {
    width: 100%;
    height: 100%;
    padding: 25px 0;
    overflow: auto;
    position: absolute;
    top: 0;
    left: 0;
  }

  &__footer {
    display: flex;
    margin-top: 24px;
  }
}

.welcome {
  display: flex;
  flex-direction: column;
  height: 100%;
  text-align: justify;

  &__footer {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding-top: 8px;
    margin-top: auto;
  }

  &__info {
    position: relative;
    top: 8px;
  }
}

.socials {
  display: flex;

  &__item {
    display: flex;

    &:not(:last-child) {
      margin-right: 6px;
    }

    img {
      height: 25px;
    }
  }
}

.page-tabs {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 8px;
  margin-bottom: 8px;

  .nav-link {
    display: block;
    font-size: 18px;
    color: $white;
    background-color: #a0a09f;
    text-align: center;
    padding: 8px 10px 5px;
    transition: 0.3s;

    &.active {
      background-color: $primary;
    }

    &:hover {
      color: $primary;
      background-color: $yellow;
    }
  }
}

.contact-us {
  font-size: 30px;
  line-height: 1.45;
  text-align: center;
  color: #000;
  padding-top: 53px;
  padding: 50px 0;

  a {
    color: $primary;

    &:hover {
      text-decoration: none;
    }
  }
}

.seemore {
  font-size: 20px;
  color: #0095ff;
  text-decoration: underline;
  font-weight: 400;

  &:hover {
    color: #0095ff;
    text-decoration: underline;
  }
}

.kind-of-service {
  display: flex;
  flex-direction: column;
  height: 100%;

  ul {
    padding-left: 14px;

    li:not(:last-child) {
      margin-bottom: 9px;
    }
  }
}

.service-tuts {
  display: flex;
  flex-direction: column;
  min-height: 390px;
  background-color: #e3e1e1;
  padding: 14px;

  &__header {
    min-height: 30px;
    background-color: $white;
  }

  &__body {
    padding: 20px;
    margin-top: 14px;
    border: 1px solid $border-color;
    background-color: $white;
    flex-grow: 1;

    ul {
      padding-left: 28px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.service-step {
  &__block {
    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }

  &__label {
    display: inline-block;
    background-color: $primary;
    color: $white;
    border: 1px solid $primary;
    text-transform: uppercase;
    line-height: 26px;
    padding: 2px 30px 0;
    margin-bottom: 12px;

    &:hover {
      color: $white;
      text-decoration: none;
    }
  }

  &__text {
    font-weight: 700;

    a:hover {
      color: $primary;
      text-decoration: none;
    }
  }

  &__downloads {
    display: flex;
    flex-wrap: wrap;
    margin-top: 12px;
  }

  &__download {
    align-items: center;
    font-size: 12px;
    line-height: 1.2;

    &,
    &:hover {
      color: inherit;
      text-decoration: none;
    }

    &:not(:last-child) {
      margin-right: 40px;
    }

    img {
      height: 45px;
      margin-right: 10px;
    }
  }
}

.advise-form-banner {
  height: 100%;

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 50% 50%;
  }
}

.captcha-text {
  color: $primary;
  font-size: 25px;
  line-height: 31px;
  padding-left: 24px;
  padding-top: 2px;
}

.cate {
  line-height: 26px;
  list-style: none;
  margin: 0;
  padding: 0;

  &__item {
    &:not(:last-child) {
      margin-bottom: 2px;
    }
  }

  &__text {
    font-size: 14px;
    font-weight: 700;
    color: $primary;
  }

  &__sub {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__sub-text {
    padding-left: 16px;
    position: relative;

    &::before {
      content: "";
      border: 4px solid transparent;
      border-width: 3px 6px;
      border-left-color: $gray-800;
      position: absolute;
      top: 8px;
      left: 0;
    }
  }

  &__sub-2 {
    padding-left: 26px;
    list-style-type: disc;
  }
}

.as-post {
  background-color: #f7f6f6;
  padding: 58px 30px 125px;

  &__title {
    font-size: 30px;
    font-weight: 400;
    line-height: 1.2;
    text-align: center;
    color: $primary;
    margin-bottom: 30px;
  }

  &__desc {
    text-align: center;
    margin-bottom: 50px;
  }

  &__download {
    align-items: center;

    &,
    &:hover {
      color: inherit;
      text-decoration: none;
    }

    img {
      height: 60px;
      margin-right: 6px;
    }
  }

  &--2 {
    padding: 16px;
    height: 100%;
  }

  &--2 &__title {
    margin-bottom: 10px;
  }

  &--2 &__desc {
    margin-bottom: 12px;
  }
}

.post {
  &-title {
    font-size: 18px;
    font-weight: 700;
    color: $primary;
    margin-bottom: 0;
  }

  &-info {
    color: #747474;
    margin-bottom: 20px;
    font-size: 13px;
    line-height: 21px;
  }

  &-sapo {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 20px;
  }

  &-content {
    text-align: justify;

    ul:not(:last-child),
    ol:not(:last-child),
    p:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}

.download {
  display: inline-flex;
  align-items: center;
  font-size: 14px;
  line-height: 1.2;

  &,
  &:hover {
    color: inherit;
    text-decoration: none;
  }
}

.evaluate-investment-location {
  table {
    width: 100%;
    border: 1px solid $border-color;
    color: #747474;

    thead {
      th {
        font-size: 16px;
        text-align: center;
        color: $white;
        background-color: #a0a09f;
        height: 40px;
        vertical-align: middle;
        border-left: 1px solid $white;
        border-right: 1px solid $white;
        font-weight: 400;

        &:first-child {
          border-left: 0;
        }

        &:last-child {
          border-right: 0;
        }

        &:nth-child(n + 3) {
          width: 155px;
        }

        a,
        a:hover {
          display: block;
          color: $white;
          text-decoration: none;
          padding: 8px 12px;
        }
      }
    }

    tbody {
      td {
        border: 1px solid $border-color;
        text-align: center;
        padding: 8px 12px;
        vertical-align: middle;

        &:nth-child(2) {
          text-align: left;
        }
      }
    }

    tfoot {
      td {
        height: 60px;
        border: 1px solid $border-color;
        text-align: center;
        padding: 8px 12px;
        vertical-align: middle;
      }
    }
  }

  .stars::before {
    font-size: 20px;
    color: $primary !important;
  }
}

.city-detail {
  border-top: 1px solid $border-color;

  td {
    vertical-align: baseline;
    padding: 8px 0;

    &:first-child {
      width: 210px;
      padding-right: 24px;
      font-weight: 700;
    }
  }
}

.map-vn {
  position: relative;
  z-index: 1;
}

.map-vn-marker {
  display: none;
  width: 20px;
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;
  transform: translate(-50%, -100%);
}

#my-calendar {
  background-color: #f6f6f6;
  min-height: 200px;
  position: relative;
  overflow: hidden;

  .calendar-event {
    position: absolute;
    z-index: 30;
    top: 100%;
    left: 0;
    width: 100%;
    height: 100%;
    transition: 0.3s;

    &.show {
      top: 0;
    }

    .card-body {
      overflow-y: auto;
    }

    .close-btn {
      cursor: pointer;
    }
  }

  .clndr {
    &-controls {
      display: flex;
      min-height: 45px;
      align-items: center;
      padding: 1px 1px 0;

      .month {
        flex: 1;
        text-align: center;
      }
    }

    &-control-button {
      display: flex;
      align-self: stretch;
      width: calc(100% / 7);
      justify-content: center;
      align-items: center;
      cursor: pointer;
      position: relative;
      background-color: #f6f6f6;
      box-shadow: 0 0 0 1px #dcdcdc;

      &::before {
        content: "\f104";
        font-family: "fontawesome";
      }

      &:last-child {
        border-right: 0;

        &::before {
          content: "\f105";
        }
      }
    }

    &-previous-button,
    &-next-button {
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 10;
      opacity: 0;
    }

    &-table {
      border-collapse: collapse;
      width: 100%;
      text-align: center;

      .header-day {
        font-weight: bold;
        font-size: 13px;
        height: 40px;
        border: 1px solid #dcdcdc;
      }

      .day {
        vertical-align: top;
        height: 55px;
        border: 1px solid #dcdcdc;
        background-color: #fff;
        cursor: pointer;
      }

      .day.event > .day-contents {
        background-color: #1e73be;
        color: #fff;
      }

      .day.today {
        background-color: #ffe0e0;
      }

      .day-contents {
        font-size: 12px;
        font-weight: bold;
        color: #666;
        background-color: #e2e2e2;
      }

      .last-month,
      .next-month {
        opacity: 0;
      }
    }
  }
}

